),_c('br'),_vm._v("\n      尽管汇聘已经尽可能提供了全面的安全防范措施，以下情况仍然有可能发生，例如某第三方躲过了我们的安全措施并进入我们的数据库，查找到您的个人信息，盗取您的个人信息或对其进行修改。汇聘认为在使用汇聘提供的服务、向汇聘提供个人信息、将简历放入我们的数据库时，您已经意识到了这种风险的存在，并同意承担这样的风险。对于因此而引起的任何法律纠纷，汇聘不承担任何法律责任。\n    ")]),_vm._v(" "),_c('b',[_vm._v("七、隐私政策的适用范围")]),_vm._v(" "),_c('p',[_vm._v("本《隐私政策》适用汇聘软件内的所有服务。但是针对某些特定服务的隐私政策，我们将更具体地说明在该服务中如何使用您的信息。如相关特定服务的隐私政策与本《隐私政策》有不一致之处，适用该特定服务的隐私政策。"),_c('br'),_vm._v("\n      请您注意，本《隐私政策》不适用于以下情况："),_c('br'),_vm._v("\n      a.通过我们的服务而接入的第三方服务（包括任何第三方网站）收集的信息；"),_c('br'),_vm._v("\n      b.通过在我们服务中进行广告服务的其他公司或机构所收集的信息。\n    ")]),_vm._v(" "),_c('b',[_vm._v("八、变更")]),_vm._v(" "),_c('p',[_vm._v("我们可能适时修订本《隐私政策》的条款，我们将在修订生效前通过在软件内显著位置提示，或向您发送电子邮件，或以其他方式通知您。在向您进行通知之后，若您继续使用我们的服务，即表示同意受经修订的本《隐私政策》的约束。")])])])}]
